.quote-container {
  width: 100%;
  max-width: 800px;
  margin-bottom: 30px;
  padding: 20px;
  border-radius: 15px;
  height: 250px;
  display: flex;
  flex-direction: column;

  .quote-header{
    display: flex;
    align-items: center;
    justify-content: space-between;

    i{
      color:white;
      font-size: 1.3em;
    }
  }

  .affirmation {
    color: white;
    font-family: "Roboto";
    font-size:1.7em;
    margin-bottom: 10px;
  }

  .quote {
    font-size: 0.9em;
    color: white;
    display: inline-block;
    margin-bottom: 20px;
    font-family: "Roboto";
  }

  .quote-icon-container {
    display: flex;
    display: none;
  }

  .quote-icon {
    width: 15px;
    margin-right: 35px;
    cursor: pointer;
    fill: black;
  }

  .verse{
    color:white;
    font-weight: bold;
    font-family: 'Roboto';
  }

  .refresh {
    transition: ease 0.5s;
    &.spin {
      animation: spin 0.5s linear;
    }

    @keyframes spin {
      from {
        transform: rotate(0);
        fill: white;
      }
      to {
        transform: rotate(360deg);
        fill: #42cdca;
      }
    }
  }

  .tweet {
    color: black;
  }
}
